import Vue from 'vue';
import Router from 'vue-router';
import CropImage from '../components/CropImage';
import Login from '../components/Login';
import File from '../components/File';
import Iframe from '../components/Iframe';
import MainLayout from '../components/MainLayout';

import MbNetworks from '../components/Modbus/MbNetworks';
import MbDevices from '../components/Modbus/MbDevices';
import MbPoints from '../components/Modbus/MbPoints';

import UpdateService from '../components/UpdateService';
import Scanning from '../components/Scanning/Scanning';
import Setting from '../components/Setting/Setting.vue';
import Services from '../components/Services/Services';
import BacNetServer from '../components/Bacnet/BacNetServer';
import Exception403 from '../components/Exception403';
import LoRaNetwork from '../components/Lora/LoRaNetwork';
import RubixApps from '../components/RubixApps/RubixApps';
import Mqtt from '../components/Mqtt/Mqtt';
import CloudNetworks from '../components/Cloud/CloudNetworks';
import CloudDevices from '../components/Cloud/CloudDevices';
import CloudPoints from '../components/Cloud/CloudPoints';
import Logging from '../components/Logging';
import ApiClient from '../components/ApiClient/ApiClient';
import LoRaNetworkPointsTable from '../components/Lora/LoRaNetworkPointsTable';
import AppManager from '../components/AppManager';
import ServiceMapping from '../components/ServiceMapping/ServiceMapping.vue';
import IO10Config from '../components/io-config/io-10-config.vue';

Vue.use(Router);

let router = new Router({
  mode: 'history',
  routes: [
    { path: '/login', name: 'login', component: Login, meta: { guest: true } },
    {
      path: '',
      name: 'main',
      component: MainLayout,
      meta: { auth: true },
      children: [
        { path: '', redirect: 'services' },
        { path: 'services', name: 'services', component: Services, meta: { settingKey: 'services' } },
        { path: 'file', name: 'file', component: File, meta: { settingKey: 'fileManager' } },
        { path: 'bacnet-server', name: 'bacnet-server', component: BacNetServer, meta: { settingKey: 'bacnetServer' } },
        { path: 'modbus', name: 'modbus', component: MbNetworks, meta: { settingKey: 'modbus' } },
        { path: 'devices', name: 'devices', component: MbDevices },
        { path: 'api-client', name: 'apiClient', component: ApiClient, meta: { settingKey: 'apiClient' } },
        { path: 'points', name: 'points', component: MbPoints },
        { path: 'ip-scanning', name: 'ipScanning', component: Scanning, meta: { settingKey: 'ipScanning' } },
        {
          path: 'cloud-networks',
          name: 'cloud-networks',
          component: CloudNetworks,
          meta: { settingKey: 'cloudNetworks' },
        },
        { path: 'cloud-devices', name: 'cloud-devices', component: CloudDevices },
        { path: 'cloud-points', name: 'cloud-points', component: CloudPoints },
        { path: 'logging', name: 'logging', component: Logging, meta: { settingKey: 'logging' } },
        { path: 'iframe/:id', name: 'wires', component: Iframe },
        { path: 'update-service/:id', name: 'wires', component: UpdateService },
        { path: 'crop-image', name: 'Crop Image', component: CropImage },
        {
          path: 'service-mapping',
          name: 'Service Mapping',
          component: ServiceMapping,
          meta: { settingKey: 'serviceMapping' },
        },
        { path: 'mqtt', name: 'MQTT', component: Mqtt, meta: { settingKey: 'mqtt' } },
        { path: 'setting', name: 'Setting', component: Setting },
        { path: 'exception-403', name: 'exception-403', component: Exception403 },
        { path: 'loRa-network', name: 'loRa-network', component: LoRaNetwork },
        { path: 'loRa-network-points', name: 'loRa-network-points', component: LoRaNetworkPointsTable },
        { path: 'app-manager', name: 'app-manager', component: AppManager },
        { path: 'rubix-apps', name: 'rubix-apps', component: RubixApps },
        { path: 'io-10-config', name: 'io-10-config', component: IO10Config },
      ],
    },
  ],
});

export default router;
