<template>
  <v-card>
    <v-card-title>
      <span class="headline">New Input</span>
    </v-card-title>
    <v-card-text>
      <v-container>
        <v-form v-model="valid" ref="form" lazy-validation>
          <v-row>
            <v-col cols="12" sm="6" md="4">
              <v-text-field v-model="formData.name" label="Name" required :rules="[rules.required]"></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="4">
              <v-select v-model="formData.point" label="Point" :items="Object.keys(ui_point_register)" required :rules="[rules.required]"></v-select>
            </v-col>
          </v-row>
        </v-form>
      </v-container>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="darken-1" text @click="onClose"> Cancel </v-btn>
      <v-btn color="blue darken-1" :disabled="!valid" text @click="onSave"> Save </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
  export default {
    props: ['device_uuid', 'onCloseInputForm', 'saveUO_UI'],
    data: () => ({
      valid: false,
      ui_point_register : { "UI1": 1, "UI2": 3, "UI3": 5, "UI4": 7, "UI5": 9, "UI6": 11 },
      formData:  {},
      rules: {
        required: value => !!value || 'Required.',
      },
    }),
    computed: {
    },
    methods: {
     async onSave() {
        if (this.$refs.form.validate()) {
          const postData = {
            name: this.formData.name,
            enable: true,
            register: this.ui_point_register[this.formData.point],
            register_length: 2,
            cov_threshold: 0.01,
            function_code: 'READ_INPUT_REGISTERS',
            data_type: 'FLOAT',
            data_endian: "BEB_LEW",
            device_uuid: this.device_uuid
          };
          //post input endpoint
          await this.saveUO_UI(postData);
          this.onClose();
        }
      },
      onClose(){
        this.formData = {};
        this.onCloseInputForm();
      }
    },
  };
</script>
