<template>
  <div>
    <v-row align="center">
        <v-col cols="4" sm="4" md="4">
            <v-select :items="network_rtu_ports" v-model="network_rtu_port" label="Serial Port"></v-select>
        </v-col>
        <v-col cols="4" sm="4" md="4">
            <v-text-field label="Device Address" type="number" v-model="device_address"></v-text-field>
        </v-col>
    </v-row>
    <!-- UO table -->
    <v-data-table :headers="headers" :items="UOs" class="elevation-1" disable-sort>
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>Universal Output</v-toolbar-title>
        </v-toolbar>
      </template>
      <template v-slot:item.point_write_value="{ item }">
        <v-select :items="UOPointValues" style="width:150px" v-model="item.point_write_value"></v-select>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-btn small @click="SetConfigUO(item, 'UO')" color="primary" :disabled="!network_rtu_port || !device_address" text>
          <v-icon small>mdi-send </v-icon>
          <span> SET</span>
        </v-btn>&nbsp;
        <v-btn small @click="getConfigUO(item, 'UO')" color="primary" :disabled="!network_rtu_port || !device_address" text>
            <v-icon small>mdi-send</v-icon>
            <span> GET</span>
        </v-btn>&nbsp;
      </template>
    </v-data-table>
    <br />
    <!-- UI table -->
    <v-data-table :headers="headers" :items="UIs" class="elevation-1" disable-sort>
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>Universal Input</v-toolbar-title>
        </v-toolbar>
      </template>
      <template v-slot:item.point_write_value="{ item }">
        <v-select :items="UIPointValues" style="width:150px" v-model="item.point_write_value"></v-select>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-btn small @click="SetConfigUI(item, 'UI')" color="primary" :disabled="!network_rtu_port || !device_address" text>
            <v-icon small>mdi-send</v-icon>
               <span> SET</span>
        </v-btn>&nbsp;
          <v-btn small @click="getConfigUI(item, 'UI')" color="primary" :disabled="!network_rtu_port || !device_address" text>
            <v-icon small>mdi-send</v-icon>
            <span> GET</span>
        </v-btn>&nbsp;
      </template>
    </v-data-table>
  </div>
</template>

<script>
import axios from "../../axios";

const UO_point_register = { "UO1": 5, "UO2": 6 };
const UO_point_write_value = { "RAW": 0, "0-10VDC (AO)": 1, "0-12VDC (DO)": 2 };
const UI_point_register = { "UI1": 7, "UI2": 8, "UI3": 9, "UI4": 10, "UI5": 11, "UI6": 12 };
const UI_point_write_value = { "RAW (DI)": 0, "0-10VDC (AI)": 1, "10k (resistance)": 2, "10k (type 2 temp)": 3, "20k (na on IO-10)": 4, "4-20MA (na on IO-10)": 5, "Pulse Count (na on IO-10)": 6 };

export default {
  name: 'io-10-config',
  data: () => ({
    network_rtu_port: '/dev/ttyUSB0',
    device_address: null,
    network_rtu_ports: [ "/dev/ttyUSB0", "/dev/ttyUSB1", "/dev/ttyUSB2", "/dev/ttyUSB3", "/dev/ttyUSB4" ],
    headers: [
      { text: 'Name', value: 'name', align: 'start' },
      { text: 'Point Register', value: 'point_register' },
      { text: 'Point Write Value', value: 'point_write_value'},
      { text: 'Actions', value: 'actions' },
    ],
    UOs: [
      { name: 'UO-1-Config (register 5)', point_register: "UO1", point_write_value: 'RAW' },
      { name: 'UO-2-Config (register 6)', point_register: "UO2", point_write_value: 'RAW' },
    ],
    UIs: [
      { name: 'UI-1-Config (register 7)', point_register: 'UI1', point_write_value: 'RAW' },
      { name: 'UI-2-Config (register 8)', point_register: 'UI2', point_write_value: 'RAW' },
      { name: 'UI-3-Config (register 9)', point_register: 'UI3', point_write_value: 'RAW' },
      { name: 'UI-4-Config (register 10)', point_register: 'UI4', point_write_value: 'RAW' },
      { name: 'UI-5-Config (register 11)', point_register: 'UI5', point_write_value: 'RAW' },
      { name: 'UI-6-Config (register 12)', point_register: 'UI6', point_write_value: 'RAW' },
    ],
    UOPointValues: Object.keys(UO_point_write_value),
    UIPointValues: Object.keys(UI_point_write_value),

  }),
  mounted() {},
  watch: {},
  mounted() {
  },
  methods: {
    SetConfigUO(item, type){
        const postData = {
            network_type: "RTU", 
            network_rtu_port: this.network_rtu_port,  
            device_address: Number(this.device_address),  
            point_register: type == 'UO' ? UO_point_register[item.point_register] : UI_point_register[item.point_register],   
            point_register_length: 1, 
            point_function_code: "WRITE_REGISTER", 
            point_data_type: "INT16", 
            point_write_value: type == 'UO' ? UO_point_write_value[item.point_write_value] : UI_point_write_value[item.point_write_value]
        }
        axios.post(`point-server/poll-point`, postData )
        .then((response) => {
          const uoType = this.uoType(response.value_original)
          this.$toasted.show(`Update Success:${uoType}`);
        });
    },
    SetConfigUI(item, type){
        const postData = {
            network_type: "RTU", 
            network_rtu_port: this.network_rtu_port,  
            device_address: Number(this.device_address),  
            point_register: type == 'UO' ? UO_point_register[item.point_register] : UI_point_register[item.point_register],   
            point_register_length: 1, 
            point_function_code: "WRITE_REGISTER", 
            point_data_type: "INT16", 
            point_write_value: type == 'UO' ? UO_point_write_value[item.point_write_value] : UI_point_write_value[item.point_write_value]
        }
        axios.post(`point-server/poll-point`, postData )
        .then((response) => {
          const uiType = this.uiType(response.value_original)
          this.$toasted.show(`Update Success:${uiType}`);
        });
    },
    getConfigUO(item, type){
        const postData = {
            network_type: "RTU", 
            network_rtu_port: this.network_rtu_port,  
            device_address: Number(this.device_address),  
            point_register: type == 'UO' ? UO_point_register[item.point_register] : UI_point_register[item.point_register],   
            point_register_length: 1, 
            point_function_code: "READ_HOLDING_REGISTERS", 
            point_data_type: "INT16",         }
        axios.post(`point-server/poll-point`, postData )
        .then((response) => {
          const uoType = this.uoType(response.value_original)
          this.$toasted.show(`Update Success:${uoType}`);
        });
    },
    getConfigUI(item, type){
        const postData = {
            network_type: "RTU", 
            network_rtu_port: this.network_rtu_port,  
            device_address: Number(this.device_address),  
            point_register: type == 'UO' ? UO_point_register[item.point_register] : UI_point_register[item.point_register],   
            point_register_length: 1, 
            point_function_code: "READ_HOLDING_REGISTERS", 
            point_data_type: "INT16",         }
        axios.post(`point-server/poll-point`, postData )
        .then((response) => {
          const uiType = this.uiType(response.value_original)
          this.$toasted.show(`Update Success:${uiType}`);
        });
    },
    uoType(val) {
          if (val == 0) {
            return "Output Type: RAW"
          } else if(val == 1) {
            return "Output Type: 0-10VDC (AO)"
          } else if(val == 2) {
            return "Output Type: 0-12VDC (DO)"
          }
      },
      uiType(val) {
          if (val == 0) {
            return "Output Type: RAW"
          } else if(val == 1) {
            return "Output Type: 0-10VDC (AO)"
          } else if(val == 2) {
            return "Output Type: 10k (resistance)"
          } else if(val == 3) {
            return "Output Type: 10k (type 2 temp)"
          } else if(val == 4) {
            return "Output Type: 20k (na on IO-10)"
          } else if(val == 5) {
            return "Output Type: 4-20MA (na on IO-10)"
          } else if(val == 6) {
            return "Output Type: Pulse Count (na on IO-10)"
          }
      },  
  },
  computed: {
    setting() {
      return this.$store.state.setting;
    },
  },
};
</script>

