<template>
  <v-card-text>
    <parent-child-mapping-form
      :serviceMappings="serviceMappings"
      :parentPoints="loraPoints"
      :child1Points="bacnetPoints"
      :child2Points="genericPoints"
      parentMappingKey="loraPoint"
      child1MappingKey="bacnetPoint"
      child2MappingKey="genericPoint"
      :createServiceMapping="createServiceMapping"
      parentFieldTitle="LoRa Points"
      child1FieldTitle="BACnet Points"
      child2FieldTitle="Rubix Points"
    >
    </parent-child-mapping-form>
    <v-divider></v-divider>
    <parent-child-mapping-grid
      :loading="isFetchingGridData"
      file-name="lora-bacnet-generic-service-mapping"
      :format-data-service="formatExportData"
      :parentPoints="loraPoints"
      :child1Points="bacnetPoints"
      :child2Points="genericPoints"
      parentMappingKey="loraPoint"
      child1MappingKey="bacnetPoint"
      child2MappingKey="genericPoint"
      :serviceMappings="serviceMappings"
      :deleteItem="deleteServiceMapping"
      parentHeaderTitle="LoRa Points"
      child1HeaderTitle="BACnet Points"
      child2HeaderTitle="Rubix Points"
      :updateServiceMapping="updateServiceMapping"
    ></parent-child-mapping-grid>
  </v-card-text>
</template>
<script>
import ParentChildMappingForm from './ParentChildMappingForm';
import ParentChildMappingGrid from './ParentChildMappingGrid';
import * as pointServerService from '../../services/pointServerService';
import * as loraBacnetGenericPointsService from '../../services/loraBacnetGenericPointsService';
import {
  CREATE_MAPPING_SERVICE,
  DELETE_MAPPING_SERVICE,
  UPDATE_MAPPING_SERVICE,
} from '../../constants/messageConstants';

export default {
  name: 'lora-generic-bacnet-points-mapping',
  components: {
    ParentChildMappingForm,
    ParentChildMappingGrid,
  },
  data() {
    return {
      isUpdating: false,
      serviceMappings: [],
      genericPoints: [],
      bacnetPoints: [],
      loraPoints: [],
      isFetchingGridData: false,
    };
  },
  mounted() {
    this.fetchServiceMapping();

    pointServerService
      .fetchRubixPSGenericPoints()
      .then(data => {
        this.genericPoints = data;
      })
      .catch(err => {});

    pointServerService
      .fetchBacnetPoints()
      .then(data => {
        this.bacnetPoints = data.map(item => ({ id: item.uuid, name: item.object_name }));
      })
      .catch(err => {});

    pointServerService
      .fetchLoraPoints()
      .then(data => {
        this.loraPoints = data;
      })
      .catch(err => {});
  },
  methods: {
    fetchServiceMapping() {
      this.isFetchingGridData = true;
      return loraBacnetGenericPointsService
        .fetch()
        .then(data => {
          this.serviceMappings = data;
        })
        .finally(() => {
          this.isFetchingGridData = false;
        });
    },
    createServiceMapping(mapping) {
      return loraBacnetGenericPointsService
        .createServiceMapping(mapping)
        .then(() => {
          this.fetchServiceMapping();
          this.$toasted.show(CREATE_MAPPING_SERVICE.success);
        })
        .catch(err => {
          this.$toasted.show(CREATE_MAPPING_SERVICE.error, { type: 'error' });
        })
        .finally(() => {});
    },
    updateServiceMapping(mapping, uuid) {
      return loraBacnetGenericPointsService
        .updateServiceMapping(mapping, uuid)
        .then(() => {
          this.fetchServiceMapping();
          this.$toasted.show(UPDATE_MAPPING_SERVICE.success);
        })
        .catch(err => {
          this.$toasted.show(UPDATE_MAPPING_SERVICE.error, { type: 'error' });
        });
    },
    deleteServiceMapping(serviceMapping) {
      return loraBacnetGenericPointsService
        .deleteServiceMapping(serviceMapping)
        .then(() => {
          this.fetchServiceMapping();
          this.$toasted.show(DELETE_MAPPING_SERVICE.success);
        })
        .catch(err => {
          this.$toasted.show(DELETE_MAPPING_SERVICE.error, { type: 'error' });
          console.error(err);
        });
    },
    formatExportData(dataItems) {
      return dataItems;
    },
  },
};
</script>
