<template>
  <div>
    <v-row :key="index" v-for="(item, index) of items">
      <v-col cols="5">
        <v-text-field @input="(v) => onChange(v, 'key', index)" v-model="item.key" label="Key" ></v-text-field>
      </v-col>
      <v-col cols="5">
        <v-text-field @input="(v) => onChange(v, 'value', index)" v-model="item.value" label="Value" ></v-text-field>
      </v-col>
      <v-col class="wrap-remove" cols="2">
        <v-btn icon @click="clickRemove(index)">
          <v-icon>mdi-close-circle</v-icon>
        </v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <v-btn block small @click="clickAdd">
          Add
        </v-btn>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  props: ['items', 'onChange'],
  methods: {
      clickAdd() {
        this.items.push({});
      },
      clickRemove(index) {
        this.items.splice(index, 1);
      }
  }
};
</script>
<style>
  .v-card__actions {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .wrap-remove {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .v-autocomplete__content.v-menu__content {
    text-align: left;
  }
</style>
