<template>
  <div>
    <v-tabs v-model="tab" fixed-tabs dark>
      <v-tab :key="0">
        LoRa Network
      </v-tab>
      <v-tab :key="1">
        Serial NETWORKS
      </v-tab>
      <v-tab :key="2">
        Debug Messages
      </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="0">
        <lo-ra-network-table ref="loraTable"></lo-ra-network-table>
      </v-tab-item>
      <v-tab-item :key="1">
        <lo-ra-serial-networks-form></lo-ra-serial-networks-form>
      </v-tab-item>
      <v-tab-item :key="2">
        <debug-messages :topic="topic"></debug-messages>
      </v-tab-item>
    </v-tabs-items>

  </div>
</template>

<script>

import LoRaNetworkTable from './LoRaNetworkTable.vue';
import LoRaSerialNetworksForm from './LoRaSerialNetworksForm.vue';
import DebugMessages from '../Common/DebugMessages.vue';
import axios from "../../axios";

export default {
  components: {
    LoRaSerialNetworksForm,
    DebugMessages,
    LoRaNetworkTable,
  },
  data: () => ({
    topic: 'rubix/lora_raw/raw',
    tab: 0,
    tabs: ['table', 'port', 'debug-message'],
  }),
  beforeRouteLeave (to, from, next) {
    this.$refs.loraTable.prepareToExit();
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).then(r => {
      next();
    });
  },
  watch: {
    '$route.query.tab':function(val){
      this.tab = this.tabs.indexOf(val);
    },
    tab: function (val, oldVal) {
      if(oldVal == 0){
        this.$refs.loraTable.prepareToExit();
      }
      if(val == 0){
        this.$refs.loraTable.autoRefresh();
      }
      const { tab } = this.$route.query;
      if (tab !== this.tabs[val]) {
        const tabKey = this.tabs[val];
        this.$router.push({ path: 'loRa-network', query: { tab: tabKey } });

      }
    },
  },
  mounted(){
    const { tab: tabKey } = this.$route.query;
    this.tab = this.tabs.indexOf(tabKey);
  },
};
</script>
