<template>
  <div>

    <v-tabs v-model="tab" fixed-tabs dark>
      <v-tab :key="0">
        Data
      </v-tab>
      <v-tab :key="1">
        Debug Messages
      </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="0">
        <v-data-table
          v-model="selected"
          :headers="deviceHeaders"
          :items="network.devices"
          class="elevation-1"
          item-key="uuid"
          show-select
          :item-class="backgroundColor"
          :footer-props="{
        showFirstLastPage: true,
        firstIcon: 'mdi-chevron-double-left',
        lastIcon: 'mdi-chevron-double-right',
        prevIcon: 'mdi-chevron-left',
        nextIcon: 'mdi-chevron-right',
      }"
          :search="search"
        >
          <template v-slot:top>
            <v-breadcrumbs :items="dirs" large>
              <v-breadcrumbs-item v-bind:class="{ 'cursor-pointer': !item.disabled }" slot="item" slot-scope="{ item }" @click.native="() => clickBreadcrumb(item)" :key="item.text" >
                {{ item.text }}
              </v-breadcrumbs-item>
            </v-breadcrumbs>
            <v-toolbar flat>
              <v-toolbar-title>DEVICES</v-toolbar-title>
              <v-divider class="mx-4" inset vertical></v-divider>
              <div class="text-left pa-2">
                <v-btn @click="getData" x-small color="primary" class="white--text"> Refresh<v-icon x-small right dark> mdi-refresh </v-icon> </v-btn>&nbsp;
                <v-btn @click="dialog = true" x-small color="success" class="white--text"> Create<v-icon x-small right dark> mdi-plus </v-icon> </v-btn >&nbsp;
                <v-btn @click="clickDeleteMulti" :disabled="selected.length === 0" x-small color="error" class="white--text" > Delete<v-icon x-small right dark> mdi-delete </v-icon> </v-btn >&nbsp;
                <v-menu offset-y>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn x-small color="#009688" class="white--text" dark v-bind="attrs" v-on="on"> Download<v-icon x-small right dark>mdi-download</v-icon> </v-btn>
                  </template>
                  <v-list>
                    <v-list-item link>
                      <v-list-item-title @click="clickDownload(network.devices, 'json')">Json</v-list-item-title>
                    </v-list-item>
                    <v-list-item link>
                      <v-list-item-title @click="clickDownload(network.devices, 'excel')">Excel</v-list-item-title>
                    </v-list-item>
                    <v-list-item link>
                      <v-list-item-title @click="clickDownload(network.devices, 'csv')">CSV</v-list-item-title>
                    </v-list-item>
                  </v-list>
                </v-menu>&nbsp;
                <v-menu offset-y>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn x-small color="primary" class="white--text" v-bind="attrs" v-on="on">
                      Import<v-icon x-small right dark>mdi-import</v-icon>
                    </v-btn>
                  </template>
                  <v-list>
                    <v-list-item link>
                      <v-list-item-title @click="clickImportJson">Json</v-list-item-title>
                    </v-list-item>
                    <v-list-item link>
                      <v-list-item-title @click=" dialogImportExcel = true; file = null; " >Excel</v-list-item-title >
                    </v-list-item>
                  </v-list>
                </v-menu >&nbsp;
                <v-btn @click="clickJsonExport(selected)" :disabled="selected.length === 0" x-small color="#009688" class="white--text" > Export<v-icon x-small right dark> mdi-export </v-icon> </v-btn>
              </div>
              <v-spacer></v-spacer>
              <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
            </v-toolbar>
          </template>

          <template v-slot:item.name="{ item }">
            <a @click="onClickOpen(item)"> {{ item.name }} </a>
          </template>

          <template v-slot:item.enable="{ item }" >
            <v-checkbox v-model="item.enable" @click="updateDevice(item)"></v-checkbox>
          </template>

          <template v-slot:item.address="{ item }">
            <v-edit-dialog :return-value.sync="item.address" @save="updateDevice(item)" @cancel="cancelEdit" @open="openEdit(item)" @close="closeEdit" large >
              <b>{{ item.address }}</b>
              <template v-slot:input>
                <v-text-field v-model="item.address" single-line type='number' min="0" @input="numberChange()" :rules="[() => !isExistingvalue(item.address, 'address') || 'Address Existed']"></v-text-field>
              </template>
            </v-edit-dialog>
          </template>

          <template v-slot:item.tcp_ip="{ item }">
            <v-edit-dialog :return-value.sync="item.tcp_ip" @save="updateDevice(item)" @cancel="cancelEdit" @open="openEdit(item)" @close="closeEdit" large >
              <b>{{ item.tcp_ip }}</b>
              <template v-slot:input>
                <v-form v-model="valid" ref="form" lazy-validation>
                  <v-text-field v-model="item.tcp_ip" single-line :rules="[() => !isExistingvalue(item.tcp_ip, 'tcp_ip') || 'IP Existed']"></v-text-field>
                </v-form>
              </template>
            </v-edit-dialog>
          </template>

          <template v-slot:item.tcp_port="{ item }">
            <v-edit-dialog :return-value.sync="item.tcp_port" @save="updateDevice(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
              <b>{{ item.tcp_port }}</b>
              <template v-slot:input>
                <v-text-field v-model="item.tcp_port" single-line type='number' min="0" @input="numberChange()"></v-text-field>
              </template>
            </v-edit-dialog>
          </template>

          <template v-slot:item.ping_point="{ item }">
            <v-edit-dialog :return-value.sync="item.ping_point" @save="updateDevice(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
              <b>{{ item.ping_point }}</b>
              <template v-slot:input>
                <v-text-field v-model="item.ping_point" single-line></v-text-field>
              </template>
            </v-edit-dialog>
          </template>

          <template v-slot:item.history_enable="{ item }">
            <v-checkbox v-model="item.history_enable" @click="updateDevice(item)"></v-checkbox>
          </template>

          <template v-slot:header.enable="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('enable') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('enable')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.tcp_port="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('port') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('tcp_port')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.ping_point="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('ping_point') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('ping_point')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>
          <template v-slot:header.history_enable="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('history_enable') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('history_enable')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:item.actions="{ item }">
            <v-icon small @click="editItem(item)" color="primary">mdi-pencil</v-icon>&nbsp;
            <v-icon small @click="clickDelete(item)" color="error">mdi-delete</v-icon>&nbsp;
            <v-icon small @click="onClickOpen(item)">mdi-open-in-app</v-icon>
          </template>
          <template v-slot:no-data> No data </template>
        </v-data-table>
      </v-tab-item>
      <v-tab-item :key="2">
        <debug-messages :topic="topic"></debug-messages>
      </v-tab-item>
    </v-tabs-items>

    <!-- Create/Edit dialog-->
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field v-model="network.name" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.name" label="device name" required :rules="[() => !isExistingvalue(editedItem.name, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                </v-col>
                <!-- <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.type" label="Type" :items="['RTU', 'TCP']" required :rules="[rules.required]" ></v-select>
                </v-col> -->
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.enable" label="enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4" v-if="network.type == 'TCP'">
                  <v-text-field v-model="editedItem.tcp_ip" label="IP" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4" v-if="network.type == 'TCP'">
                  <v-text-field v-model="editedItem.tcp_port" label="Port" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.zero_mode" label="zero mode"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                  <v-text-field v-model="editedItem.ping_point" label="ping point (Func:Addr:Len 1:1:1)"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.timeout_global" label="timeout global"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.timeout" label="timeout" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.address" label="address" type="number" :rules="[() => !isExistingvalue(editedItem.address, 'address') || 'Address Existed']" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.history_enable" label="History Enable"></v-checkbox>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import Json dialog-->
    <v-dialog v-model="dialogImport">
      <v-toolbar flat dark>
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon dark @click="closeDialogImport">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1"> Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2"> Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="clickContinue()" :disabled="!dataImport"> Continue </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="deviceHeaders"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="uuid"
                :footer-props="{
                  showFirstLastPage: true,
                  firstIcon: 'mdi-chevron-double-left',
                  lastIcon: 'mdi-chevron-double-right',
                  prevIcon: 'mdi-chevron-left',
                  nextIcon: 'mdi-chevron-right',
                }"
                :search="searchWizard"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="searchWizard" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                  </v-toolbar>
                </template>

                <template v-slot:item.actions="{ item }">
                  <v-icon small @click="clickDeleteImport(item)" color="error"> mdi-delete </v-icon>
                </template>
                <template v-slot:item.name="{ item }">
                  <v-text-field v-model="item.name" single-line style="width: 100px" required :rules="[() => !isExistingvalue(item.name, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                </template>
                <template v-slot:item.address="{ item }">
                  <v-text-field v-model="item.address" type="number" single-line style="width: 100px"></v-text-field>
                </template>
                <template v-slot:item.tcp_ip="{ item }">
                  <v-text-field v-if="network.type == 'TCP'" v-model="item.tcp_ip" single-line style="width: 100px"></v-text-field>
                </template>
                <template v-slot:item.tcp_port="{ item }">
                  <v-text-field v-if="network.type == 'TCP'" v-model="item.tcp_port" type="number" single-line style="width: 100px"></v-text-field>
                </template>
                <template v-slot:item.points="{ item }">
                  <div>{{ item.points ? item.points.length : 0 }}</div>
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1 = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImport" :disabled="!valid"> Save </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <v-dialog v-model="dialogImportExcel" width="500px">
      <v-card>
        <v-card-title>Choose excel file</v-card-title>
        <v-card-text>
          <v-file-input outlined dense label="File input" v-model="file" accept=".xls,.xlsx"></v-file-input>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogImportExcel = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="convertExcelToJson" :disabled="!file">Convert</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Delete dialog -->
    <v-dialog v-model="dialogDelete" persistent max-width="290">
      <v-card>
        <v-card-title> Confirm Delete Device</v-card-title>
        <v-card-text>
          <v-container>
            <v-row>Are you sure?</v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDelete"> No </v-btn>
          <v-btn color="red darken-1" text @click="confirm"> Yes </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="300px">
        <v-card>
          <v-card-title>Mass Edit</v-card-title>
          <v-card-text>
            <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
            <v-text-field v-if="typeof editAll[editField] === 'number'" min="0" v-model.number="editAll[editField]"  :label="toUpperTextKey(editField)" type="number" @input="numberChange()"></v-text-field>
            <v-text-field v-if="typeof editAll[editField] === 'string'" v-model="editAll[editField]" :label="toUpperTextKey(editField)" ></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
            <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
          </v-card-actions>
        </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { copyToClipboard, isJsonString, exportJSON, exportCSV, exportExcel, toUpperTextKey } from '../../utils/utils';
import MbPoints from '../Modbus/MbPoints';
import axios from '../../axios';
import * as xlsx from 'xlsx';
import _cloneDeep from 'lodash/cloneDeep';
import _forIn from 'lodash/forIn';
import DebugMessages from '../Common/DebugMessages.vue';

export default {
  components: { 'points-table': MbPoints, DebugMessages },
  data: () => ({
    topic: 'rubix/points/debug',
    tab: 0,
    valid: false,
    dialog: false,
    dialogDelete: false,
    deviceHeaders: [
      { text: 'Actions', value: 'actions', sortable: false },
      { text: 'Name', value: 'name', align: 'start' },
      { text: 'Fault', value: 'fault', sortable: false },
      { text: 'Type', value: 'type', sortable: false },
      { text: 'Enable', value: 'enable', sortable: false },
      { text: 'Address', value: 'address', sortable: false },
      { text: 'IP', value: 'tcp_ip', sortable: false },
      { text: 'Port', value: 'tcp_port', sortable: false },
      { text: 'Ping Point', value: 'ping_point', sortable: false },
      { text: 'History enable', value: 'history_enable', sortable: false },
      { text: 'UUID', value: 'uuid', align: 'start', sortable: false },
    ],
    editedIndex: -1,
    editedItem: {
      name: '',
      // type: 'RTU',
      enable: true,
      address: 0,
      tcp_ip: '',
      tcp_port: 502,
      ping_point: '',
      zero_mode: false,
      timeout: 0.5,
      timeout_global: false,
      history_enable: false,
      network_uuid: '',
    },
    defaultItem: {
      name: '',
      // type: 'RTU',
      enable: true,
      address: 0,
      tcp_ip: '',
      tcp_port: 502,
      ping_point: '',
      zero_mode: false,
      timeout: 0.5,
      timeout_global: false,
      history_enable: false,
      network_uuid: '',
    },
    currentItem: {},
    dataImport: '',
    rules: { required: value => !!value || 'Required.' },
    network: {},
    selected: [],
    action: '',
    objectKeys: Object.keys,
    objectValues: Object.values,
    dirs: [{ text: '..', link: 'modbus' }],
    dataImport: '',
    importArray: [],
    dialogImport: false,
    e1: 1,
    allPoints: [],
    dialogImportExcel: false,
    file: null,
    search: '',
    searchWizard: '',
    dialogMassEdit: false,
    editAll: {
      enable: false,
      tcp_port: 0,
      ping_point: "",
      history_enable: false,
    },
    editField: "",
    intervalId: null,

  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Create Device' : 'Edit Device';
    },
    refreshTime() {
      const { setting } = this.$store.state;
      return setting.refreshTime;
    },
    enableAutoRefresh() {
      const { setting } = this.$store.state;
      return setting.enableAutoRefresh;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).then(r => {
      next();
    });
  },
  mounted() {
    this.getData();

    //set auto refresh
    if(this.refreshTime &&  this.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getData();
      }, this.refreshTime);
    }
  },
  methods: {
    async getData() {
      await axios.get(`point-server/network/${this.$route.query.network_uuid}?with_children=true`).then(data => {
        this.network = data;
        //get dir
        if (this.dirs.length != 2) {
          (this.dirs = [{ text: '..', link: 'modbus' }]), this.dirs.push({ text: this.network.name, disabled: true });
        }
        this.editedItem.network_uuid = this.defaultItem.network_uuid = this.network.uuid;
        //change headers
        if(this.network.type == "RTU"){
          const indexIP = this.deviceHeaders.findIndex(h=>h.value=='tcp_ip');
          if(indexIP > -1){
            this.deviceHeaders.splice(indexIP, 1);
          }
          const indexPort = this.deviceHeaders.findIndex(h=>h.value=='tcp_port');
          if(indexPort > -1){
            this.deviceHeaders.splice(indexPort, 1);
          }
        }
      });
    },
    onClickOpen(item) {
      this.$router.push({ name: 'points', query: { device_uuid: item.uuid } });
    },
    clickBreadcrumb(dir) {
      if (!dir.disabled) {
        this.$router.push({ name: dir.link });
      }
    },
    editItem(item) {
      this.editedIndex = this.network.devices.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    async save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          this.updateDevice(this.editedItem);
        } else {
          this.createDevice(this.editedItem);
        }
      }
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.network.devices.indexOf(this.currentItem);
      this.network.devices.splice(this.editedIndex, 1);
      await axios.delete(`point-server/device/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    clickImportJson() {
      this.dialogImport = true;
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.selected) : '';
      this.e1 = 1;
    },
    convertExcelToJson() {
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(this.file);
      fileReader.onload = event => {
        let data = event.target.result;
        let workbook = xlsx.read(data, { type: 'binary' });
        workbook.SheetNames.forEach(sheet => {
          let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
          const items = rowObject.map(i => {
            for (let key in i) {
              const newKey = key.trimRight('');
              const value = i[key];
              delete i[key];
              i = { ...i, [newKey]: value };
            }
            i.network_uuid = this.network.uuid;
            i = this.getPointsArray(i);
            return i;
          });
          this.dataImport = JSON.stringify(items);
          this.dialogImportExcel = false;
          this.dialogImport = true;
          this.e1 = 1;
        });
      };
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    async saveImport() {
      if (this.importArray && this.importArray.length > 0) {
        if (this.$refs.form.validate()) {
          const promises = [];
          for (const item of this.importArray) {
            delete item.uuid;
            item.network_uuid = this.network.uuid; //update to the current network uuid
            await this.createDevice(item);
          }
          this.closeDialogImport();
          this.selected = [];
        }
      }
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.$nextTick(() => {
        this.dataImport = '';
      });
    },
    clickContinue() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport);
      this.e1 = 2;
    },
    isExistingvalue(value, fieldName) {
      const devices = _cloneDeep(this.network.devices);
      if (this.editedIndex !== -1) {
        //when edit
        devices.splice(this.editedIndex, 1);
      }
      if (fieldName === 'address' || fieldName === 'tcp_port') {
        value = Number(value);
      }
      return devices.find(d => d[fieldName] == value) != null;
    },
    clickJsonExport() {
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(array, type) {
      const data = this.convertToExportData(array);
      if (type === 'csv') {
        return exportCSV('devices', data);
      }
      if (type === 'excel') {
        return exportExcel('devices', data);
      }
      if (type === 'json') {
        return exportJSON('devices', JSON.stringify(this.network.devices));
      }
    },
    convertToExportData(array) {
      const data = _cloneDeep(array).map(device => {
        _forIn(device, (value, key) => {
          if (key == 'points') {
            //get points
            device.points.forEach((point, indexP) => {
              _forIn(point, (point_value, point_key) => {
                if (point_key != 'point_store') {
                  const pointKey = `point[${indexP}].${point_key}`;
                  device = { ...device, [pointKey]: point_value };
                } else {
                  //point_store
                  _forIn(point.point_store, (point_store_value, point_store_key) => {
                    const pointStoreKey = `point[${indexP}].point_store.${point_store_key}`;
                    device = { ...device, [pointStoreKey]: point_store_value };
                  });
                  delete point.point_store;
                }
              });
            });
            delete device.points;
          }
        });
        return device;
      });
      return data;
    },
    getPointsArray(device) {
      let points = [];
      let indexs = [];
      let pointKeys = [];
      Object.keys(device).forEach(key => {
        let keys = key.split('.');
        let pointKey = '';
        let pointIndex = null;
        if (keys.length > 1) {
          pointKey = keys[1];
          if (!pointKeys.includes(pointKey)) {
            pointKeys.push(pointKey);
          }
          pointIndex = Number(keys[0].split('[')[1].slice(0, -1));
          if ((pointIndex || pointIndex === 0) && !indexs.includes(pointIndex)) {
            indexs.push(pointIndex);
          }
        }
      });
      indexs.forEach(i => {
        let point = {};
        pointKeys.forEach(pk => {
          point = { ...point, [pk]: device[`point[${i}].${pk}`] };
        });
        points.push(point);
      });
      device.points = points;
      return device;
    },
    updateDevice(data) {
      axios.put(`point-server/device/${data.uuid}`, data).then(response => {
        const index = this.network.devices.findIndex(d => d.uuid == response.uuid);
        Object.assign(this.network.devices[index], response);
        this.close();
        return this.$toasted.show('update success');
      });
    },
    createDevice(data) {
      if(this.network.type == 'RTU'){
        delete data.tcp_ip;
        delete data.tcp_port;
      }
      axios.post(`point-server/device`, data).then(response => {
        this.network.devices.push(response);
        this.close();
        this.$toasted.show('create success');
        if (data.points && data.points.length > 0) {
          for (const point of data.points) {
            point.device_uuid = response.uuid;
            point.register = point.register ? point.register : 0;
            point.register_length = point.register_length ? point.register_length : 0;
            point.enable = point.enable ? point.enable : false;
            this.createPoint(point);
          }
        }
      });
    },
    async createPoint(data) {
      axios.post(`point-server/point`, data).then(response => {
        const index = this.network.devices.findIndex(p => p.uuid == response.device_uuid);
        this.network.devices[index].points.push(response);
      });
    },
    backgroundColor(item) {
      let classColor = '';
      if (item.fault) {
        classColor = 'background-fault-true';
      } else {
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor;
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updateDevice(item);
      }
      this.refresh();
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
    },
    refresh(){
      // this.selected = [];
      this.editAll = {
        enable: false,
        tcp_port: 0,
        ping_point: "",
        history_enable: false,
      }
      this.dialogMassEdit = false;
    },
    toUpperTextKey(key) {
      if(!key) return;
      return toUpperTextKey(key);
    },
    numberChange(){
      if(!this.editAll[this.editField])
        return this.editAll[this.editField] = 0;
    },
    cancelEdit() { },
    openEdit(item) {
      this.editedIndex = this.network.devices.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
     },
  },
};
</script>
<style scoped>
.centered-input >>> input {
  text-align: center;
}
.cursor-pointer {
  cursor: pointer;
  color: #1867c0;
}
.background-fault-true {
  background-color: #fbb93e;
}
.background-enable-false {
  background-color: #666666;
}
</style>
