<template>
  <v-dialog v-model="isOpen" persistent>
    <v-card>
      <v-card-title>
        <span class="headline">Import Mappings</span>
      </v-card-title>
      <v-card-text>
        <v-form v-model="valid" ref="form" lazy-validation>
          <v-data-table
            :headers="headers"
            :items="dataItems"
            class="elevation-1 mb-4"
            :footer-props="{
              showFirstLastPage: true,
              firstIcon: 'mdi-chevron-double-left',
              lastIcon: 'mdi-chevron-double-right',
              prevIcon: 'mdi-chevron-left',
              nextIcon: 'mdi-chevron-right',
            }"
          >
            <template v-slot:item.actions="{ item }">
              <v-icon small @click="clickDeleteImport(item.index)" color="error"> mdi-delete </v-icon>
            </template>
            <template v-slot:item.rowNumber="{ item, index }">
              <span>{{ +index + 1 }}</span>
            </template>
            <template v-slot:item.networkName="{ item }">
              <v-text-field
                v-model="item.network_details.network.name"
                single-line
                style="width: 100px"
                required
              ></v-text-field>
            </template>
            <template v-slot:item.deviceName="{ item }">
              <v-text-field
                v-model="item.network_details.device.name"
                single-line
                style="width: 100px"
                required
              ></v-text-field>
            </template>
            <template v-slot:item.pointName="{ item }">
              <v-text-field
                v-model="item.network_details.point.name"
                single-line
                style="width: 100px"
                :rules="[rules.required]"
                required
              ></v-text-field>
            </template>
            <template v-slot:item.bacnetPointName="{ item }">
              <v-text-field
                v-model="item.bacnet_point.name"
                :rules="[rules.required]"
                single-line
                style="width: 100px"
                required
              ></v-text-field>
            </template>
            <template v-slot:item.bacnetPointAddress="{ item }">
              <v-text-field
                v-model="item.bacnet_point.address"
                single-line
                style="width: 100px"
                required
                :rules="[rules.required]"
              ></v-text-field>
            </template>
          </v-data-table>
        </v-form>
      </v-card-text>
      <v-card v-if="errors.length >= 1" class="px-8 mt-n15">
        <v-card-title color="red darken-1">Errors</v-card-title>
        <v-card-text>
          <v-data-table hide-default-footer :headers="errorTableHeaders" :items="errors" class="elevation-1 mb-1">
          </v-data-table>
        </v-card-text>
      </v-card>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="darken-1" text @click="closeDialog"> Cancel </v-btn>
        <v-btn color="blue darken-1" :loading="isCreatingMapping" text @click="createMapping"> Save </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
import _get from 'lodash/get';
import * as genericBacnetPointsMappingService from '../../services/genericBacnetPointsMappingService';
export default {
  name: 'bacnet-generic-point-mapping-dialog',
  props: ['dataRows', 'isOpen', 'close', 'bacnetPoints', 'genericPoints', 'syncMappingBacnetGenericPoints'],
  data: () => ({
    valid: false,
    isRequestOnce: false,
    isCreatingMapping: false,
    errors: [],
    rules: {
      required: value => !!value || 'Required.',
    },
    errorTableHeaders: [
      { text: 'Row', value: 'rowIndex', sortable: false },
      { text: 'Error Message', value: 'message', sortable: false },
    ],
    headers: [
      { text: 'Actions', value: 'actions', sortable: false },
      { text: 'Row', value: 'rowNumber', sortable: false },
      { text: 'Network Name', value: 'networkName', sortable: false },
      { text: 'Device Name', value: 'deviceName', sortable: false },
      { text: 'Point Name', value: 'pointName', sortable: false },
      { text: 'Bacnet Point Name', value: 'bacnetPointName', sortable: false },
      { text: 'Bacnet Point Address', value: 'bacnetPointAddress', sortable: false },
    ],
  }),
  computed: {
    dataItems() {
      return this.dataRows;
    },
    genericPointNames() {
      return this.genericPoints.map(item => _get(item, 'point.name')).filter(item => item);
    },
  },
  methods: {
    exists(value, items, fieldName) {
      if (!items) {
        return false;
      }
      const foundItem = items.find(item => (fieldName ? item[fieldName] == value : item == value));
      return !!foundItem;
    },
    clickDeleteImport(index) {
      this.dataRows = this.dataRows.filter((item, itemIndex) => itemIndex !== index);
      this.errors = this.errors.filter((item, index) => itemIndex !== index);
    },
    createMapping() {
      if (!this.$refs.form.validate()) {
        return;
      }
      this.isCreatingMapping = true;
      this.isRequestOnce = true;
      genericBacnetPointsMappingService
        .createMappingFromFileUpload(this.dataItems)
        .then(results => {
          this.errors = results.reduce((acc, currResult) => {
            if (!currResult.success) {
              acc.push(this.getErrorFromResult(currResult));
            }
            return acc;
          }, []);
          const areAllSuccess = results.map(result => result.success).every(item => item);
          if (areAllSuccess) {
            this.syncMappingBacnetGenericPoints();
            this.closeDialog();
          }
        })
        .catch(err => {
          console.log(err);
        })
        .finally(() => {
          this.isCreatingMapping = false;
        });
    },
    getErrorFromResult(result) {
      return {
        rowIndex: result.rowIndex + 1,
        message: result.message,
      };
    },
    closeDialog() {
      if (this.isRequestOnce) {
        this.syncMappingBacnetGenericPoints();
      }
      this.close();
    },
  },
};
</script>
