<template>
  <div>

    <v-tabs v-model="tab" fixed-tabs dark>
      <v-tab :key="0">
        Data
      </v-tab>
      <v-tab :key="1">
        Debug Messages
      </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="0">
        <v-data-table
          v-model="selected"
          :headers="headers"
          :items="networks"
          class="elevation-1"
          item-key="uuid"
          show-select
          :item-class="backgroundColor"
          :footer-props="{
        showFirstLastPage: true,
        firstIcon: 'mdi-chevron-double-left',
        lastIcon: 'mdi-chevron-double-right',
        prevIcon: 'mdi-chevron-left',
        nextIcon: 'mdi-chevron-right'
      }"
          :search="search"
        >
          <template v-slot:top >
            <v-breadcrumbs large>..</v-breadcrumbs>
            <v-toolbar flat>
              <v-toolbar-title>NETWORKS</v-toolbar-title>
              <v-divider class="mx-4" inset vertical></v-divider>
              <div class="text-left pa-2">
                <v-btn @click="getNetworks" x-small color="primary" class="white--text" > Refresh <v-icon x-small right dark> mdi-refresh </v-icon> </v-btn>&nbsp;
                <v-btn @click="dialog = true" x-small color="success" class="white--text" > Create <v-icon x-small right dark> mdi-plus </v-icon> </v-btn>&nbsp;
                <v-btn @click="clickDeleteMulti" :disabled="selected.length === 0" x-small color="error" class="white--text"> Delete <v-icon x-small right dark > mdi-delete </v-icon> </v-btn>&nbsp;
                <v-menu offset-y>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn x-small color="#009688" class="white--text"  dark v-bind="attrs" v-on="on" >Download<v-icon x-small right dark>mdi-download</v-icon></v-btn>
                  </template>
                  <v-list>
                    <v-list-item link>
                      <v-list-item-title @click="clickDownload(networks, 'json')">Json</v-list-item-title>
                    </v-list-item>
                    <v-list-item link>
                      <v-list-item-title @click="clickDownload(networks, 'excel')">Excel</v-list-item-title>
                    </v-list-item>
                    <v-list-item link>
                      <v-list-item-title @click="clickDownload(networks,'csv')">CSV</v-list-item-title>
                    </v-list-item>
                  </v-list>
                </v-menu>&nbsp;
                <v-menu offset-y>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn x-small color="primary" class="white--text" v-bind="attrs" v-on="on" >Import<v-icon x-small right dark>mdi-import</v-icon></v-btn>
                  </template>
                  <v-list>
                    <v-list-item link>
                      <v-list-item-title @click="clickImportJson">Json</v-list-item-title>
                    </v-list-item>
                    <v-list-item link>
                      <v-list-item-title @click="dialogImportExcel = true; file = null">Excel</v-list-item-title>
                    </v-list-item>
                  </v-list>
                </v-menu>&nbsp;
                <v-btn @click="clickJsonExport(selected)" :disabled="selected.length !== 1" x-small color="#009688" class="white--text" > Export<v-icon x-small right dark> mdi-export </v-icon></v-btn>
              </div>
              <v-spacer></v-spacer>
              <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
            </v-toolbar>
          </template>

          <template v-slot:item.name="{ item }">
            <a @click="onClickOpen(item)"> {{ item.name }} </a>
          </template>

          <template v-slot:item.enable="{ item }" >
            <v-checkbox v-model="item.enable" @click="updateNetwork(item)"></v-checkbox>
          </template>

          <template v-slot:item.history_enable="{ item }">
            <v-checkbox v-model="item.history_enable" @click="updateNetwork(item)"></v-checkbox>
          </template>

          <template v-slot:item.timeout="{ item }">
            <v-edit-dialog :return-value.sync="item.timeout" @save="updateNetwork(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
              <b>{{ item.timeout }}</b>
              <template v-slot:input>
                <v-text-field v-model="item.timeout" single-line type='number' min="0" @input="numberChange()"></v-text-field>
              </template>
            </v-edit-dialog>
          </template>

          <template v-slot:item.device_timeout_global="{ item }">
            <v-edit-dialog :return-value.sync="item.device_timeout_global" @save="updateNetwork(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
              <b>{{ item.device_timeout_global }}</b>
              <template v-slot:input>
                <v-text-field v-model="item.device_timeout_global" single-line type='number' min="0" @input="numberChange()"></v-text-field>
              </template>
            </v-edit-dialog>
          </template>

          <template v-slot:item.point_timeout_global="{ item }">
            <v-edit-dialog :return-value.sync="item.point_timeout_global" @save="updateNetwork(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
              <b>{{ item.point_timeout_global }}</b>
              <template v-slot:input>
                <v-text-field v-model="item.point_timeout_global" single-line type='number' min="0" @input="numberChange()"></v-text-field>
              </template>
            </v-edit-dialog>
          </template>

          <template v-slot:header.enable="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('enable') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('enable')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.timeout="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('timeout') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('timeout')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.device_timeout_global="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('device_timeout_global') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('device_timeout_global')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.point_timeout_global="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('point_timeout_global') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('point_timeout_global')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.history_enable="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('history_enable') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('history_enable')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:item.actions="{ item }" >
            <v-icon small @click="editItem(item)" color="primary">mdi-pencil</v-icon>&nbsp;
            <v-icon small @click="clickDelete(item)" color="error">mdi-delete</v-icon>&nbsp;
            <v-icon small @click="onClickOpen(item)">mdi-open-in-app</v-icon>
          </template>
          <template v-slot:no-data> No data </template>
        </v-data-table>
      </v-tab-item>
      <v-tab-item :key="2">
        <debug-messages :topic="topic"></debug-messages>
      </v-tab-item>
    </v-tabs-items>

    <!-- create/edit dialog -->
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.name" single-line label="Network name" required :rules="[() => !isExistingvalue(editedItem.name, 'name') || 'Name Existed', rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.type" label="Type" :items="['RTU', 'TCP']" required :rules="[rules.required]"></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.enable" label="Enable" required></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.timeout" label="Timeout" required :rules="[rules.required]" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.device_timeout_global" label="Timeout global" required :rules="[rules.required]" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.point_timeout_global" label="Point timeout global" required :rules="[rules.required]" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-checkbox v-model="editedItem.history_enable" label="history enable"></v-checkbox>
                </v-col>
              </v-row>
              <v-row v-if="editedItem.type === 'RTU'">
                 <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.rtu_parity" label="rtu parity" item-text="name" item-value="value" :items="rtu_paritys" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.rtu_speed" label="rtu speed" :items="[9600, 19200, 38400, 57600, 115200]" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.rtu_stop_bits" label="rtu stopbits" :items="[1, 1.5, 2]" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.rtu_byte_size" label="rtu bytesize" :items="[5, 6, 7, 8]" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-combobox v-model="editedItem.rtu_port" :items="rtu_ports" label="rtu port" :rules="[() => !isExistingvalue(editedItem.rtu_port, 'rtu_port') || 'Port Existed', rules.required]"></v-combobox>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import Json dialog-->
    <v-dialog v-model="dialogImport" >
      <v-toolbar flat dark >
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon dark @click="closeDialogImport" >
            <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1" > Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2" > Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea
                    outlined
                    name="input-7-4"
                    label="import data"
                    v-model="dataImport"
                  ></v-textarea>

                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="clickContinue()" :disabled="!dataImport"> Continue </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="headers"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="uuid"
                :footer-props="{
                  showFirstLastPage: true,
                  firstIcon: 'mdi-chevron-double-left',
                  lastIcon: 'mdi-chevron-double-right',
                  prevIcon: 'mdi-chevron-left',
                  nextIcon: 'mdi-chevron-right'
                }"
                :search="searchWizard"
              >
                <template v-slot:top >
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="searchWizard" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                  </v-toolbar>
                </template>
                <template v-slot:item.actions="{ item }">
                  <v-icon small @click="clickDeleteImport(item)" color="error"> mdi-delete </v-icon>
                </template>
                <template v-slot:item.name="{ item }" >
                  <v-text-field v-model="item.name" single-line style="width:100px" required :rules="[() => !isExistingvalue(item.name, 'name') || 'Name Existed', rules.required]"></v-text-field>
                </template>
                <template v-slot:item.rtu_port="{ item }" >
                  <v-combobox v-model="item.rtu_port" :items="rtu_ports" :rules="[() => !isExistingvalue(item.rtu_port, 'rtu_port') || 'Port Existed', rules.required]"></v-combobox>
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1 = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImport" :disabled="!valid"> Save </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <v-dialog v-model="dialogImportExcel" width="500px">
        <v-card>
          <v-card-title>Choose excel file</v-card-title>
          <v-card-text>
            <v-file-input outlined dense label="File input" v-model="file" accept=".xls,.xlsx"></v-file-input>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="darken-1" text @click="dialogImportExcel = false">Close</v-btn>
            <v-btn color="primary darken-1" text @click="convertExcelToJson" :disabled="!file">Convert</v-btn>
          </v-card-actions>
        </v-card>
    </v-dialog>

    <!-- delete dialog -->
    <v-dialog v-model="dialogDelete" persistent max-width="290">
      <v-card>
        <v-card-title> Confirm Delete Network </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>Are you sure?</v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDelete"> No </v-btn>
          <v-btn color="red darken-1" text @click="confirm">
            Yes
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="300px">
        <v-card>
          <v-card-title>Mass Edit</v-card-title>
          <v-card-text>
            <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
            <v-text-field v-if="typeof editAll[editField] === 'number'" min="0" v-model.number="editAll[editField]"  :label="toUpperTextKey(editField)" type="number" @input="numberChange()"></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
            <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
          </v-card-actions>
        </v-card>
    </v-dialog>

  </div>
</template>

<script>
import { copyToClipboard, isJsonString, exportJSON, exportCSV, exportExcel, toUpperTextKey } from "../../utils/utils";
import axios from "../../axios";
import * as xlsx from "xlsx";
import _cloneDeep from 'lodash/cloneDeep';
import _forIn from 'lodash/forIn';
import DebugMessages from '../Common/DebugMessages.vue';

const pause = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

export default {
  components: {
    DebugMessages
  },
  data: () => ({
    topic: 'rubix/points/debug',
    tab: 0,
    valid: false,
    dialog: false,
    dialogDelete: false,
    selected: [],
    headers: [
      { text: "Actions", value: "actions", sortable: false },
      { text: "Name", value: "name", align: "start" },
      { text: "Fault", value: "fault", sortable: false },
      { text: "Type", value: "type", sortable: false },
      { text: "Enable", value: "enable", sortable: false },
      { text: "Timeout", value: "timeout", sortable: false },
      { text: "Device timout global", value: "device_timeout_global", sortable: false },
      { text: "Point timeout global", value: "point_timeout_global", sortable: false },
      { text: 'History enable', value: 'history_enable', sortable: false },
      { text: "Port", value: "rtu_port", sortable: false },
      // { text: "Devices", value: "devices", sortable: false },
      { text: "UUID", value: "uuid", align: "start", sortable: false },
    ],
    networks: [],
    rtu_paritys:[
      { name : "even", value: "E" },
      { name : "odd", value: "O" },
      { name : "none", value: "N" },
    ],
    rtu_ports:[ "/dev/ttyUSB0", "/dev/ttyUSB1", "/dev/ttyUSB2", "/dev/ttyUSB3", "/dev/ttyUSB4" ],
    currentItem: {},
    editedIndex: -1,
    editedItem: {
      name: "",
      type: "RTU",
      enable: true,
      timeout: 0.5,
      device_timeout_global: 0.5,
      point_timeout_global:0.5,
      rtu_port: "/dev/ttyUSB2",
      rtu_speed: 38400,
      rtu_stop_bits: 1,
      rtu_parity: 'N',
      rtu_byte_size: 8,
      history_enable: false,
    },
    defaultItem: {
      name: "",
      type: "RTU",
      enable: true,
      timeout: 0.5,
      device_timeout_global: 0.5,
      point_timeout_global:0.5,
      rtu_port: "/dev/ttyUSB2",
      rtu_speed: 38400,
      rtu_stop_bits: 1,
      rtu_parity: 'N',
      rtu_byte_size: 8,
      history_enable: false,
    },
    rules: { required: (value) => !!value || "Required." },
    action: "",
    objectKeys: Object.keys,
    objectValues: Object.values,
    dataImport: "",
    importArray: [],
    dialogImport: false,
    e1: 1,
    dialogImportExcel: false,
    file: null,
    search:"",
    searchWizard:"",
    dialogMassEdit: false,
    editAll: {
      enable: false,
      point_timeout_global: 0.5,
      device_timeout_global: 0.5,
      timeout: 0.5,
      history_enable: false,
    },
    editField: "",
    intervalId: null,
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Create Network" : "Edit Network";
    },
    pingData(){
      return this.$store.state.pingData;
    },
    refreshTime() {
      const { setting } = this.$store.state;
      return setting.refreshTime;
    },
    enableAutoRefresh() {
      const { setting } = this.$store.state;
      return setting.enableAutoRefresh;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    '$store.state.pingData':function(val){
      this.getPingData(val)
    },
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).then(r => {
      next();
    });
  },
  mounted() {
    this.getNetworks();
    if(Object.keys(this.pingData).length!==0){
      this.getPingData(this.pingData);
    }

    //set auto refresh
    if(this.refreshTime &&  this.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getNetworks();
      }, this.refreshTime);
    }
  },
  methods: {
    getPingData(data){
      if(Object.keys(data).length===0){
        this.$toasted.show('Point Server is not running',{type:"error"});
        return;
      }
    },
    getNetworks() {
      axios.get( 'point-server/network' )
        .then((data) => {
          this.networks = data;
        });
    },
    onClickOpen(item){
      this.$router.push({ name: "devices", query: { network_uuid: item.uuid } });
    },
    editItem(item) {
      this.editedIndex = this.networks.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          this.updateNetwork(this.editedItem);
        } else {
          this.createNetwork(this.editedItem);
        }
      }
    },
    async clickDelete(item) {
      this.action = "deleteSingle";
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = "deleteSelected";
    },
    async deleteSingle() {
      this.editedIndex = this.networks.indexOf(this.currentItem);
      this.networks.splice(this.editedIndex, 1);
      await axios.delete(`point-server/network/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = "";
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.$nextTick(() => {
        this.dataImport = "";
      });
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    async saveImport() {
      if(this.importArray && this.importArray.length > 0){
        if(this.$refs.form.validate()){
          const promises = [];
          for (const item of this.importArray) {
            delete item.uuid;
            await this.createNetwork(item);
          }
          this.closeDialogImport();
          this.selected = [];
        }
      }
    },
    clickImportJson(){
      this.dialogImport = true;
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.selected) : "";
      this.e1 = 1;
    },
    clickContinue(){
      if(!isJsonString(this.dataImport) ){
        return this.$toasted.show("incorrect data type!", { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport);
      this.e1 = 2;
    },
    isExistingvalue(value, fieldName) {
      let networks = _cloneDeep(this.networks);
      if(this.editedIndex !== -1){ //when edit
        networks.splice(this.editedIndex, 1);
      }
      return networks.find(n => n[fieldName] == value) != null;
    },
    convertExcelToJson(){
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(this.file);
      fileReader.onload = (event)=>{
        let data = event.target.result;
        let workbook = xlsx.read(data,{type:"binary"});
        workbook.SheetNames.forEach(sheet => {
            let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
            const items = rowObject.map((i) => {
              for(let key in i){
                const newKey = key.trimRight("");
                const value = i[key];
                delete i[key];
                i = {...i, [newKey]: value}
              }
              i.devices = this.getDevicesArray(i);

              return i;
            });
            this.dataImport = JSON.stringify(items);
            this.dialogImportExcel = false;
            this.dialogImport = true;
            this.e1 = 1;
        });
      }
    },
    getDevicesArray(network){
      let devices = [];
      let deviceIndexs = [];
      let deviceKeys = [];
      //get deviceIndexs and deviceKeys
      Object.keys(network).forEach(key =>{
        let keys = key.split('.');
        let deviceKey = "";
        let deviceIndex = null;
        if(keys.length > 1){
          deviceKey = keys[1];
          if(!deviceKeys.includes(deviceKey)){
            deviceKeys.push(deviceKey);
          }
          deviceIndex = Number(keys[0].split("[")[1].slice(0, -1));
          if((deviceIndex || deviceIndex === 0) && !deviceIndexs.includes(deviceIndex)){
            deviceIndexs.push(deviceIndex);
          }
        }
      })
      //get device
      deviceIndexs.forEach(i=>{
        let device = {};
        deviceKeys.forEach(dk=>{
          device = {...device, [dk]: network[`device[${i}].${dk}`]};
        });
        //get points of device
        Object.keys(network).forEach(key =>{
          let keys = key.split('.');
          if(keys[0] == `device[${i}]` && keys.length > 2){
            device = {...device, [`${keys[1]}.${keys[2]}`]: network[key]};
            device.points = this.getPointsArray(device);
          }
        });
        devices.push(device);
      })
      return devices;
    },
    getPointsArray(device){
      let points = [];
      let indexs = [];
      let pointKeys = [];
      Object.keys(device).forEach(key =>{
        let keys = key.split('.');
        let pointKey = "";
        let pointIndex = null;
        if(keys.length > 1){
          pointKey = keys[1];
          if(!pointKeys.includes(pointKey)){
            pointKeys.push(pointKey);
          }
          pointIndex = Number(keys[0].split("[")[1].slice(0, -1));
          if((pointIndex || pointIndex === 0) && !indexs.includes(pointIndex)){
            indexs.push(pointIndex);
          }
        }
      })
      indexs.forEach(i=>{
        let point = {};
        pointKeys.forEach(pk=>{
          point = {...point, [pk]: device[`point[${i}].${pk}`]}
        });
        points.push(point);
      })
      return points;
    },
    clickJsonExport(){
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    },
    clickDownload(array, type){
      const data = this.convertExportData(array);
      if(type === "csv"){
          return exportCSV("networks", data);
      } if(type === "excel"){
          return exportExcel("networks", data);
      } if(type === "json"){
          return exportJSON("networks", JSON.stringify(this.networks));
      }
    },
    convertExportData(array){
      const data = _cloneDeep(array).map(network=>{
        if(network.devices){
          network.devices.forEach((device, indexD)=>{
            //devices
            _forIn(device, (value, key) => {
              if(key != 'points'){
                const keyName = `device[${indexD}].${key}`;
                network = {...network, [keyName] : value}
              }else{
                //points
                device.points.forEach((point, indexP)=>{
                  _forIn(point, (point_value, point_key) => {
                    if(point_key != 'point_store'){
                        const pointKey = `device[${indexD}].point[${indexP}].${point_key}`;
                        network = {...network, [pointKey] : point_value}
                    }else{
                      //point_store
                      _forIn(point.point_store, (point_store_value, point_store_key) => {
                        const pointStoreKey = `device[${indexD}].point[${indexP}].point_store.${point_store_key}`;
                        network = {...network, [pointStoreKey] : point_store_value}
                      });
                      delete point.point_store
                    }
                  });
                });
                delete device.points
              }
            });
          });
        }
        delete network.devices
        return network;
      });
      return data;
    },
    updateNetwork(data) {
      axios.put(`point-server/network/${data.uuid}`, data )
        .then((response) => {
          const index = this.networks.findIndex(n=> n.uuid == response.uuid);
          Object.assign(this.networks[index], response);
          this.close();
          this.$toasted.show("update success");
        });
    },
    createNetwork(data) {
      if(data.type == 'TCP'){
        delete data.rtu_parity;
        delete data.rtu_speed;
        delete data.rtu_stop_bits;
        delete data.rtu_byte_size;
        delete data.rtu_port;
      }
      axios.post(`point-server/network`, data )
        .then((response) => {
          this.networks.push(response);
          if(data.devices && data.devices.length > 0){
            for (const device of data.devices) {
              device.network_uuid = response.uuid;
              device.address = device.address ? device.address : 0;
              this.createDevice(device);
            }
          }
          this.close();
          this.$toasted.show("create success");
        });
    },
    createDevice(data) {
      axios.post( `point-server/device`, data )
        .then((response) => {
          const index = this.networks.findIndex(p => p.uuid == response.network_uuid);
          this.networks[index].devices.push(response);
          if(data.points && data.points.length > 0){
            for (const point of data.points) {
              point.device_uuid = response.uuid;
              point.register = point.register ? point.register : 0;
              point.register_length = point.register_length ? point.register_length : 0;
              point.enable = point.enable ? point.enable : false;
              this.createPoint(point);
            }
          }
        });
    },
    createPoint(data) {
      axios.post( `point-server/point`, data ).then((response) => {});
    },
    backgroundColor(item) {
      let classColor = '';
      if(item.fault){
        classColor = 'background-fault-true';
      }else{
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor;
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updateNetwork(item);
      }
      this.reset();
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
    },
    reset(){
      // this.selected = [];
      this.editAll = {
        enable: false,
        point_timeout_global: 0.5,
        device_timeout_global: 0.5,
        timeout: 0.5,
        history_enable: false,
      }
      this.dialogMassEdit = false;
    },
    toUpperTextKey(key) {
      if(!key) return;
      return toUpperTextKey(key);
    },
    numberChange(){
      if(!this.editAll[this.editField])
        return this.editAll[this.editField] = 0;
    },
    cancelEdit() { },
    openEdit() { },
    closeEdit() { },

  },
};
</script>
<style>
  .background-fault-true{
    background-color: #fbb93e;
  }
  .background-enable-false{
    background-color: #666666;
  }

</style>


