<template>
  <v-card-text>
    <v-spacer></v-spacer>
    <service-mapping-form
      :service-mappings="serviceMappings"
      :mapping-list-right="rubixPSGenericPoints"
      :mapping-list-left="bacnetPoints"
      mapping-selection-right-title="Rubix Rubix Points"
      mapping-selection-left-title="BACnet Points"
      :create-service-mapping="createServiceMapping"
      mapping-left-key="bacnetPoint"
      mapping-right-key="genericPoint"
    ></service-mapping-form>
    <v-divider></v-divider>
    <service-mapping-grid
      mapping-right-key="bacnetPoint"
      mapping-left-key="genericPoint"
      :format-data-service="formatExportData"
      file-name="bacnet-points-generic-points-mapping"
      mapping-left-title="Rubix Rubix Points"
      mapping-right-title="BACnet Points"
      :mapping-list-left="rubixPSGenericPoints"
      :mapping-list-right="bacnetPoints"
      :delete-item="deleteServiceMapping"
      :delete-items="deleteServiceMappings"
      :loading="isFetchingGridData"
      :update-service-mapping="updateServiceMapping"
      :service-mappings="serviceMappings"
      :receive-data-rows="receiveDataRows"
    ></service-mapping-grid>
    <bacnet-generic-point-mapping-import-dialog
      :bacnet-points="bacnetPoints"
      :generic-points="rubixPSGenericPoints"
      :is-open="openImportMappingDialog"
      :close="closeImportMappingDialog"
      :sync-mapping-bacnet-generic-points="syncMappingBacnetGenericPoints"
      :data-rows="dataRows"
    ></bacnet-generic-point-mapping-import-dialog>
  </v-card-text>
</template>
<script>
import PageTitle from '../Common/PageTitle';
import ServiceMappingForm from './ServiceMappingForm';
import ServiceMappingGrid from './ServiceMappingGrid';
import BacnetGenericPointMappingImportDialog from './BacnetGenericPointMappingImportDialog';
import FullScreenContainer from '../Common/FullScreenContainer';
import * as pointServerService from '../../services/pointServerService';
import * as genericBacnetPointsMappingService from '../../services/genericBacnetPointsMappingService';
import {
  CREATE_MAPPING_SERVICE,
  DELETE_MAPPING_SERVICE,
  UPDATE_MAPPING_SERVICE,
  DELETE_MAPPING_SERVICES,
} from '../../constants/messageConstants';
export default {
  name: 'rubix-bacnet-points-mapping',
  components: {
    FullScreenContainer,
    ServiceMappingForm,
    ServiceMappingGrid,
    PageTitle,
    BacnetGenericPointMappingImportDialog,
  },
  props: ['on-cancel'],
  data() {
    return {
      openImportMappingDialog: false,
      isUpdating: false,
      dataRows: [],
      serviceMappings: [],
      rubixPSGenericPoints: [],
      bacnetPoints: [],
      isFetchingGridData: false,
      rubixPSGenericPointsMap: {},
      bacnetPointsMap: {},
    };
  },
  mounted() {
    this.syncMappingBacnetGenericPoints();
  },
  methods: {
    syncMappingBacnetGenericPoints() {
      this.fetchServiceMapping();
      this.fetchBacnetPoints();
      this.fetchGenericPoints();
    },
    fetchBacnetPoints() {
      pointServerService.fetchBacnetPoints().then(data => {
        this.bacnetPoints = data.map(item => ({ ...item, id: item.uuid, name: item.object_name }));
        this.bacnetPointsMap = (this.bacnetPoints || []).reduce((acc, curr) => {
          acc[curr.uuid] = curr;
          return acc;
        }, {});
      });
    },
    fetchGenericPoints() {
      pointServerService.fetchRubixPSGenericPoints().then(data => {
        this.rubixPSGenericPoints = data;
        this.rubixPSGenericPointsMap = (this.rubixPSGenericPoints || []).reduce((acc, curr) => {
          acc[curr.id] = curr;
          return acc;
        }, {});
      });
    },
    createServiceMapping(mapping) {
      return genericBacnetPointsMappingService
        .createServiceMapping(mapping)
        .then(() => {
          this.fetchServiceMapping();
          this.$toasted.show(CREATE_MAPPING_SERVICE.success);
        })
        .catch(err => {
          this.$toasted.show(CREATE_MAPPING_SERVICE.error, { type: 'error' });
        })
        .finally(() => {});
    },
    updateServiceMapping(mapping, uuid) {
      return genericBacnetPointsMappingService
        .updateServiceMapping(mapping, uuid)
        .then(() => {
          this.fetchServiceMapping();
          this.$toasted.show(UPDATE_MAPPING_SERVICE.success);
        })
        .catch(err => {
          this.$toasted.show(UPDATE_MAPPING_SERVICE.error, { type: 'error' });
        });
    },
    fetchServiceMapping() {
      this.isFetchingGridData = true;
      genericBacnetPointsMappingService
        .fetch()
        .then(data => {
          this.serviceMappings = data;
        })
        .finally(() => {
          this.isFetchingGridData = false;
        });
    },
    deleteServiceMapping(serviceMapping) {
      return genericBacnetPointsMappingService
        .deleteServiceMapping(serviceMapping)
        .then(() => {
          this.fetchServiceMapping();
          this.$toasted.show(DELETE_MAPPING_SERVICE.success);
        })
        .catch(err => {
          this.$toasted.show(DELETE_MAPPING_SERVICE.error, { type: 'error' });
          console.error(err);
        });
    },
    deleteServiceMappings(serviceMappings) {
      return genericBacnetPointsMappingService
        .deleteServiceMappings(serviceMappings)
        .then(() => {
          this.fetchServiceMapping();
          this.$toasted.show(DELETE_MAPPING_SERVICES.success);
        })
        .catch(err => {
          this.$toasted.show(DELETE_MAPPING_SERVICES.error, { type: 'error' });
        });
    },
    formatExportData(dataItems, actionType) {
      if (actionType === 'copy') {
        return dataItems
          .map(genericBacnetPointsMappingService.mapDataToRequest)
          .map(item =>
            genericBacnetPointsMappingService.appendNetworkBacnetPoints(
              item,
              this.rubixPSGenericPointsMap,
              this.bacnetPointsMap,
            ),
          );
      }
      const data = genericBacnetPointsMappingService.prepareFileExport(
        dataItems
          .map(genericBacnetPointsMappingService.mapDataToRequest)
          .map(item =>
            genericBacnetPointsMappingService.appendNetworkBacnetPoints(
              item,
              this.rubixPSGenericPointsMap,
              this.bacnetPointsMap,
            ),
          ),
      );
      return data;
    },
    closeImportMappingDialog() {
      this.openImportMappingDialog = false;
    },
    receiveDataRows(dataRows = []) {
      if (dataRows.length >= 1) {
        this.dataRows = genericBacnetPointsMappingService.prepareFileImport(dataRows);
        this.openImportMappingDialog = true;
      }
    },
  },
};
</script>
