              </template>
            </v-edit-dialog>
          </template>

          <template v-slot:item.input_max="{ item }">
            <v-edit-dialog :return-value.sync="item.input_max" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
              <b>{{ item.input_max }}</b>
              <template v-slot:input>
                <v-text-field v-model="item.input_max" single-line type='number' min="0" @input="numberChange()"></v-text-field>
              </template>
            </v-edit-dialog>
          </template>

          <template v-slot:item.input_min="{ item }">
            <v-edit-dialog :return-value.sync="item.input_min" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
              <b>{{ item.input_min }}</b>
              <template v-slot:input>
                <v-text-field v-model="item.input_min" single-line type='number' min="0" @input="numberChange()"></v-text-field>
              </template>
            </v-edit-dialog>
          </template>

          <template v-slot:item.scale_min="{ item }">
            <v-edit-dialog :return-value.sync="item.scale_min" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
              <b>{{ item.scale_min }}</b>
              <template v-slot:input>
                <v-text-field v-model="item.scale_min" single-line type='number' min="0" @input="numberChange()"></v-text-field>
              </template>
            </v-edit-dialog>
          </template>

          <template v-slot:item.scale_max="{ item }">
            <v-edit-dialog :return-value.sync="item.scale_max" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
              <b>{{ item.scale_max }}</b>
              <template v-slot:input>
                <v-text-field v-model="item.scale_max" single-line type='number' min="0" @input="numberChange()"></v-text-field>
              </template>
            </v-edit-dialog>
          </template>

          <template v-slot:item.value_offset="{ item }">
            <v-edit-dialog :return-value.sync="item.value_offset" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
              <b>{{ item.value_offset }}</b>
              <template v-slot:input>
                <v-text-field v-model="item.value_offset" single-line type='number' min="0" @input="numberChange()"></v-text-field>
              </template>
            </v-edit-dialog>
          </template>

          <template v-slot:item.value_round="{ item }">
            <v-edit-dialog :return-value.sync="item.value_round" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
              <b>{{ item.value_round }}</b>
              <template v-slot:input>
                <v-text-field v-model="item.value_round" single-line type='number' min="0" @input="numberChange()"></v-text-field>
              </template>
            </v-edit-dialog>
          </template>

          <template v-slot:item.value_operation="{ item }">
            <v-edit-dialog :return-value.sync="item.value_operation" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
              <b>{{ item.value_operation }}</b>
              <template v-slot:input>
                <v-select v-model="item.value_operation" :items="value_operation" ></v-select>
              </template>
            </v-edit-dialog>
          </template>

          <template v-slot:item.writable="{ item }">
            <v-checkbox v-model="item.writable" @click="updatePoint(item)"></v-checkbox>
          </template>

          <template v-slot:item.tags="{ item }">
            <ul style="cursor: pointer;" @click="editItem(item)" >
              <li  v-for="(key, index) of Object.keys(JSON.parse(item.tags))" :key="index">
                {{key}} : {{JSON.parse(item.tags)[key]}}
              </li>
            </ul>
          </template>

          <template v-slot:item.write_value="{ item }">
            <v-edit-dialog :return-value.sync="item.write_value" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
              <b>{{ item.write_value }}</b>
              <template v-slot:input>
                <v-text-field v-model="item.write_value" single-line type='number' min="0" @input="numberChange()"></v-text-field>
              </template>
            </v-edit-dialog>
          </template>

          <!--  -->

          <template v-slot:header.enable="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('enable') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('enable')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.history_enable="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('history_enable') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('history_enable')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.history_interval="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('history_interval') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('history_interval')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.history_type="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('history_type') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('history_type')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.cov_threshold="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('cov_threshold') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('cov_threshold')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.input_max="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('input_max') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('input_max')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.input_min="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('input_min') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('input_min')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.scale_max="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('scale_max') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('scale_max')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.scale_min="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('scale_min') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('scale_min')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.value_offset="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('value_offset') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('value_offset')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.value_operation="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('value_operation') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('value_operation')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.value_round="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('value_round') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('value_round')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.writable="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('writable') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('writable')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.write_value="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('write_value') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('write_value')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:header.tags="{ item }" >
            <div class="d-flex">
              <span>{{ toUpperTextKey('tags') }}</span>&nbsp;&nbsp;
              <v-icon x-small @click="openMassEditDialog('tags')" color="primary">mdi-pencil</v-icon>
            </div>
          </template>

          <template v-slot:item.actions="{ item }">
            <v-icon small class="mr-2" @click="editItem(item)" color="primary"> mdi-pencil </v-icon>
            <v-icon small @click="clickDelete(item)" color="error"> mdi-delete </v-icon>
          </template>

          <template v-slot:no-data> No data </template>
