<template>
  <v-card>
    <v-card-title>
      <v-btn @click="onCancel" icon medium class="white--text mr-1">
        <v-icon medium dark> mdi-arrow-left </v-icon>
      </v-btn>
      <v-container fluild class="text-left">Rubix Upgrade</v-container>
    </v-card-title>
    <v-card-text>
      <v-container fluid>
        <v-row>
          <v-col md="6" lg="6">
            <v-card elecation="2" outlined :loading="cardLoading">
              <v-card-text>
                <v-row>
                  <v-col cols="8" sm="7" class="text-left">
                    <text-field-display value="Rubix Service"></text-field-display>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="3" class="text-left">
                    <text-field-display label="Version" :value="rubixAppStatus.version"></text-field-display>
                  </v-col>
                  <v-col cols="3" class="text-left">
                    <text-field-display
                      label="Deployment Mode"
                      :value="rubixAppStatus.deployment_mode && capitalize(rubixAppStatus.deployment_mode)"
                    ></text-field-display>
                  </v-col>
                  <v-col cols="6" class="text-right">
                    <rbx-button
                      v-if="showUpgradeToLatest"
                      :click="() => handleUpgradeClick('latest')"
                      title="Upgrade to Latest"
                      color="primary"
                    ></rbx-button>
                  </v-col>
                </v-row>
                <v-form v-model="valid" ref="upgradeForm" lazy-validation>
                  <v-row>
                    <v-col cols="12">
                      <v-combobox
                        :loading="isFetchingReleases"
                        :items="rubixAppReleases"
                        label="Select a release version"
                        hideSelected
                        clearable
                        v-model="selectedVersion"
                        item-text="name"
                        item-value="tag_name"
                        :rules="versionRules"
                      >
                      </v-combobox>
                    </v-col>
                    <v-col cols="12" class="text-right">
                      <rbx-button
                        :click="() => handleUpgradeClick('version')"
                        title="Change Version"
                        color="pink"
                      ></rbx-button>
                    </v-col>
                  </v-row>
                </v-form>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-card-text>
    <confirm-dialog
      :is-open="upgradeDialog"
      :loading="isUpgrading"
      :on-close="() => closeDialog()"
      :on-confirm="() => handleDialogConfirm()"
      dialog-text="This action will logout your session! Do you want to upgrade or downgrade the app?"
    ></confirm-dialog>
  </v-card>
</template>
<script>
import RbxButton from '../Common/RbxButton';
import ConfirmDialog from '../Common/ConfirmDialog';
import validation from '../../common/validation';
import TextFieldDisplay from '../Common/TextFieldDisplay';
import * as rubixService from '../../services/rubixService';
import { RUBIX_UPGRADE } from '../../constants/messageConstants';
import * as actionTypes from '../../store/actionTypes';
import LOCAL_STORAGE from '../../constants/localStorage.js';

export default {
  name: 'rubix-upgrade-view',
  props: ['on-cancel'],
  components: { TextFieldDisplay, RbxButton, ConfirmDialog },
  data() {
    return {
      valid: false,
      upgradeDialog: false,
      isUpgrading: false,
      rubixAppStatus: {},
      rubixAppReleases: [],
      selectedVersion: null,
      cardLoading: false,
      isFetchingReleases: false,
      currentAction: null,
      showUpgradeToLatest: false,
      isLatestVersion: false,
      versionRules: [validation.required],
    };
  },
  async mounted() {
    await this.fetchRubixStatus();
    await this.fetchReleases();
    this.showUpgradeButton();
  },
  computed: {
    upgradeMethodMap() {
      return {
        version: this.upgradeVersion,
        latest: this.upgradeLatest,
      };
    },
  },
  methods: {
    fetchRubixStatus() {
      this.cardLoading = true;
      return rubixService
        .fetchStatus()
        .then(data => {
          this.$store.commit(actionTypes.UPDATE_RUBIX_SERVICE_VERSION, data);
          this.rubixAppStatus = data;
          this.selectedVersion = this.rubixAppStatus.version;
        })
        .catch(err => {})
        .finally(() => {
          this.cardLoading = false;
        });
    },
    fetchReleases() {
      this.isFetchingReleases = true;
      return rubixService
        .fetchReleases()
        .then(data => {
          this.rubixAppReleases = data;
        })
        .catch(err => {})
        .finally(() => {
          this.isFetchingReleases = false;
        });
    },
    handleUpgradeClick(action) {
      if (action === 'version' && !this.$refs.upgradeForm.validate()) {
        return;
      }
      this.upgradeDialog = true;
      this.currentAction = action;
    },
    handleDialogConfirm() {
      this.upgradeMethodMap[this.currentAction]();
    },
    upgradeLatest() {
      this.isUpgrading = true;
      rubixService
        .upgradeLatest()
        .then(() => {
          this.$toasted.show(RUBIX_UPGRADE.success);
          this.logout();
        })
        .catch(err => {
          this.$toasted.show(RUBIX_UPGRADE.error, { type: 'error' });
        })
        .finally(() => {
          this.isUpgrading = false;
          this.closeDialog();
        });
    },
    upgradeVersion() {
      this.isUpgrading = true;
      rubixService
        .upgrade(this.selectedVersion)
        .then(() => {
          this.$toasted.show(RUBIX_UPGRADE.success);
          this.logout();
        })
        .catch(err => {
          this.$toasted.show(RUBIX_UPGRADE.error, { type: 'error' });
        })
        .finally(() => {
          this.isUpgrading = false;
          this.closeDialog();
        });
    },
    capitalize(s) {
      if (typeof s !== 'string') return '';
      return s.charAt(0).toUpperCase() + s.slice(1);
    },
    closeDialog() {
      this.upgradeDialog = false;
    },
    showUpgradeButton() {
      this.showUpgradeToLatest =
        (Array.isArray(this.rubixAppReleases) &&
          this.rubixAppReleases.length > 0 &&
          this.rubixAppReleases[0] &&
          this.selectedVersion !== this.rubixAppReleases[0]) ||
        false;
    },
    logout() {
      localStorage.removeItem(LOCAL_STORAGE.JWT);
      localStorage.removeItem(LOCAL_STORAGE.TOKEN);
      localStorage.removeItem(LOCAL_STORAGE.USER);
      this.$router.push('/login');
    },
  },
};
</script>
