<template>
  <v-form v-model="valid" ref="serviceMappingForm">
    <v-row>
      <v-col :cols="!isEditForm ? 5 : 12">
        <v-autocomplete
          :items="updatedMappingListLeft"
          auto-select-first
          clearable
          v-model="selectedLeft"
          item-text="name"
          item-value="id"
          :rules="fieldValidations"
          :label="mappingSelectionLeftTitle"
          return-object
        ></v-autocomplete>
      </v-col>
      <v-col :cols="!isEditForm ? 5 : 12">
        <v-autocomplete
          :items="updatedMappingListRight"
          auto-select-first
          clearable
          v-model="selectedRight"
          item-text="name"
          :rules="fieldValidations"
          item-value="id"
          :label="mappingSelectionRightTitle"
          return-object
        ></v-autocomplete>
      </v-col>
      <v-col v-if="!isEditForm" cols="2" class="flex justify-center align-center">
        <rbx-button
          :block="true"
          :loading="isCreatingMapping"
          color="pink"
          :click="addMapping"
          title="Add"
        ></rbx-button>
      </v-col>
    </v-row>
    <v-card-actions v-if="isEditForm">
      <v-spacer></v-spacer>
      <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
      <v-btn color="blue darken-1" :loading="isCreatingMapping" text @click="addMapping"> Save </v-btn>
    </v-card-actions>
  </v-form>
</template>
<script>
import RbxButton from '../Common/RbxButton';
import validation from '../../common/validation';
export default {
  components: { RbxButton },
  props: [
    'toList',
    'fromList',
    'isEditForm',
    'onCancel',
    'serviceMapping',
    'mappingListLeft',
    'mappingListRight',
    'mappingRightKey',
    'mappingLeftKey',
    'serviceMappings',
    'createServiceMapping',
    'mappingSelectionLeftTitle',
    'mappingSelectionRightTitle',
  ],
  data() {
    return {
      uuid: null,
      valid: false,
      selectedToList: [],
      selectedLeft: null,
      selectedRight: null,
      isCreatingMapping: false,
      fieldValidations: [validation.required],
    };
  },
  mounted() {
    if (this.isEditForm && this.serviceMapping) {
      this.uuid = this.serviceMapping.uuid;
      this.selectedLeft = this.serviceMapping[this.mappingLeftKey];
      this.selectedRight = this.serviceMapping[this.mappingRightKey];
      this.fieldValidations = [];
    }
  },
  computed: {
    mappings() {
      return this.serviceMappings;
    },
    selectedMappingListLeft() {
      return this.serviceMappings.map(serviceMapping => serviceMapping[this.mappingLeftKey]);
    },
    selectedMappingListRight() {
      return this.serviceMappings.map(serviceMapping => serviceMapping[this.mappingRightKey]);
    },
    updatedMappingListLeft() {
      return this.updateLisItemProperty(this.mappingListLeft, this.selectedMappingListLeft);
    },
    updatedMappingListRight() {
      return this.updateLisItemProperty(this.mappingListRight, this.selectedMappingListRight);
    },
  },
  methods: {
    addMapping() {
      const isValid = this.$refs.serviceMappingForm.validate();
      if (!isValid) {
        return;
      }
      this.selectedMappingListLeft.push(this.selectedLeft);
      this.selectedMappingListRight.push(this.selectedRight);
      this.isCreatingMapping = true;
      this.createServiceMapping(
        {
          [this.mappingLeftKey]: this.selectedLeft,
          [this.mappingRightKey]: this.selectedRight,
        },
        this.uuid,
      )
        .then(() => {
          if (this.isEditForm) {
            return this.onCancel();
          }
          this.selectedLeft = null;
          this.selectedRight = null;
          this.$refs.serviceMappingForm.reset();
        })
        .finally(() => {
          this.isCreatingMapping = false;
        });
    },
    updateLisItemProperty(list, selectedItems) {
      if (!selectedItems) {
        return list;
      }
      const selectedListItems = selectedItems.filter(item => item).map(item => item.id);

      return list.reduce((acc, curr) => {
        if (selectedListItems.includes(curr.id)) {
          curr.disabled = true;
        } else {
          curr.disabled = false;
        }
        acc.push(curr);
        return acc;
      }, []);
    },
  },
};
</script>
<style scoped>
.flex {
  display: flex;
}
</style>
