<template>
  <div>
    <v-row>
      <v-col class="text-left" xl="3" lg="3" sm="12">
        <v-btn x-small color="primary" @click="clickImport"> Import&nbsp;<v-icon small>mdi-import</v-icon> </v-btn>
        <v-divider inset vertical></v-divider>
        <v-btn x-small @click="clickJsonExport" color="#009688" class="white--text">
          Export&nbsp;<v-icon small> mdi-export </v-icon>
        </v-btn>
      </v-col>
      <v-col xl="6" lg="6" sm="12">
        <h1>App Manager</h1>
      </v-col>
    </v-row>
    <v-tabs fixed-tabs v-model="tab">
      <v-tab :key="index" v-for="(key, index) of Object.keys(items)" style="width: unset">{{key}}</v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="index" v-for="(key, index) of Object.keys(items)">
        <v-form ref="form" lazy-validation style="padding: 20px 0">
          <v-data-table :headers="headers[key]" :items="checkType('array', items[key]) ? items[key] : [items[key]]" disable-sort class="text-left">
            <template v-slot:body="{ items, headers }">
                <tbody>
                    <tr v-for="(item,idx) in items" :key="idx">
                        <td v-for="(header,key) in headers" :key="key">
                          <v-checkbox v-model="item[header.value]" v-if="checkType('boolean',item[header.value])" small/>
                          <span v-if="header.text == 'items'" >{{item[header.value]}}</span>
                          <v-edit-dialog
                            v-if="!checkType('boolean',item[header.value]) && header.text != 'items'"
                            :return-value.sync="item[header.value]"
                            @save="saveEdit"
                            @cancel="cancel"
                            @open="open"
                            @close="close"
                            large
                          >
                            {{item[header.value]}}
                            <template v-slot:input>
                              <v-text-field v-model="item[header.value]" label="Edit" :type="`${ checkType('number',item[header.value]) ? 'number' : 'text' }`" single-line ></v-text-field>
                            </template>
                          </v-edit-dialog>
                        </td>
                    </tr>
                </tbody>
            </template>
          </v-data-table>
        </v-form>
      </v-tab-item>
    </v-tabs-items>

    <v-card-actions style="padding:10px 0">
      <v-spacer></v-spacer>
      <v-btn color="blue darken-2" @click="save">Save</v-btn>
    </v-card-actions>

    <!--Import dialog -->
    <v-dialog v-model="dialogImport" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Import</span>
        </v-card-title>
        <v-card-text>
          <v-alert class="text-left" dense colored-border type="error" elevation="2" v-show="isValidData" >Incorrect Data Type!</v-alert >
          <v-container>
            <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport" @input="onChangeImportData" ></v-textarea>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDialogImport">Cancel</v-btn>
          <v-btn color="blue darken-1" :disabled="disableSave" text v-on:click="saveImport">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Response dialog -->
    <v-dialog v-model="dialogResponse" transition="dialog-bottom-transition" max-width="600" >
      <v-card>
        <v-card-title>
          <span class="headline">Response</span>
        </v-card-title>
        <v-card-text>
          <ul>
          <li class="text-left" v-for="(response, index) of responses" :key="index">{{response}}</li>
          </ul>
        </v-card-text>
        <v-card-actions class="justify-end">
          <v-btn text @click="dialogResponse = false">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog persistent v-model="infoDialog" transition="dialog-bottom-transition" max-width="600" >
      <v-card>
        <v-card-title>
          <span class="headline">Export response</span>
        </v-card-title>
        <v-card-text>
          <v-alert class="text-left" dense colored-border type="error" elevation="2" >Can not get: {{errors.join(', ')}}</v-alert>
          <v-alert class="text-left" dense colored-border type="success" elevation="2" >Copied: {{Object.keys(resData).join(', ')}}</v-alert>
        </v-card-text>
        <v-card-actions class="justify-end">
          <v-btn text @click="infoDialog = false">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </div>
</template>

<script>
import { isJsonString, copyToClipboard, deleteIdKey } from '../utils/utils';
import axios from '../axios';
import _forEach from 'lodash/forEach';
import _forIn from 'lodash/forIn';
import _cloneDeep from 'lodash/cloneDeep';
import _isArray from 'lodash/isArray';
import _isNumber from 'lodash/isNumber';
import _isBoolean from 'lodash/isBoolean';

export default {
  data: () => ({
    tab: 0,
    tabs: [],
    items: {},
    headers: {},
    dataImport: '',
    dialogImport: false,
    infoDialog: false,
    resData: {},
    errors: [],
    isValidData: false,
    disableSave: true,
    dialogResponse : false,
    responses: [],
  }),
  computed: {
  },
  watch: {
    '$route.query.tab':function(val){
      this.tab = this.tabs.indexOf(val);
    },
    tab: function (val, oldVal) {
      const { tab } = this.$route.query;
      if (tab !== this.tabs[val]) {
        const tabKey = this.tabs[val];
        this.$router.push({ path: 'app-manager', query: { tab: tabKey } });
      }
    },
  },
  mounted() {
  },
  methods: {
    async showImportData() {
      this.tabs = Object.keys(this.items);
      _forEach(Object.keys(this.items), key => {
        if(key == "mqtt"){
          this.items[key] = this.convertMQTT(this.items[key]);
        }
        if(key == "loraDevices"){
          this.items[key] = this.convertLoraDevices(this.items[key]);
        }
        if(key == "psGenericPoints" || key == "psPoints"){
          this.items[key] = this.convertPoints(this.items[key]);
        }
        this.getHeaders(key);
      });
    },
    convertMQTT(mqtt){
      _forEach(mqtt.subscriptions, (subscription,index)=>{
        _forIn(subscription, (subscription_value, subscription_key) => {
          const newKey = `subscription[${index}].${subscription_key}`;
          mqtt = {...mqtt, [newKey] : subscription_value}
        });
      });
      delete mqtt.subscriptions;
      return mqtt;
    },
    convertLoraDevices(loraDevices){
      const data = _cloneDeep(loraDevices).map(device=>{
        delete device.points;
        delete device.uuid;
        return device;
     });
      return data;
    },
    convertPoints(points){
      const data = points.map(item => {
        delete item.point_store;
        delete item.uuid;
        return item;
      });
      return data;
    },
    getHeaders(key){
      if(_isArray(this.items[key])){
        if(this.items[key][0]){
          const headerA = Object.keys(this.items[key][0]).map(title=>{
            const data =  { text: title, value: title };
            return data;
          });
          this.headers = {...this.headers, [key]: headerA};
        }
      }else{
        const headerB = Object.keys(this.items[key]).map(title=>{
          const data =  { text: title, value: title };
          return data;
        });
        this.headers = {...this.headers, [key]: headerB};
      }
    },
    async onChangeImportData() {
      this.isValidData = this.disableSave =
        !isJsonString(this.dataImport) || Array.isArray(JSON.parse(this.dataImport));
    },
    async clickImport() {
      this.dialogImport = true;
      this.dataImport = "";
    },
    async closeDialogImport() {
      this.dialogImport = false;
    },
    async saveImport() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      const importData = JSON.parse(this.dataImport);

      //delete array or object without value
      Object.keys(importData).forEach(key=>{
        if( (_isArray(importData[key]) && importData[key].length == 0) || (!_isArray(importData[key]) && Object.keys(importData[key]).length == 0) ){
          delete importData[key]
        }
      });
      //end delete

      this.items = importData;
      this.dialogImport = false;
      this.dataImport = '';
      this.showImportData();
    },
    async clickJsonExport() {
      await axios.get('export').then(({data, errors}) => {
        copyToClipboard(JSON.stringify(data));
        this.infoDialog = true;
        this.resData = data;
        this.errors = errors;
      });
    },
    checkType(type, value){
      if(type == "array"){
        return _isArray(value);
      }
      if(type == "boolean"){
        return _isBoolean(value);
      }
      if(type == "number"){
        return _isNumber(value);
      }
    },
    saveEdit() {},
    cancel() {},
    open() {},
    close() {},
    async save() {
      const data = _cloneDeep(this.items);
      _forEach(Object.keys(data), key => {
        if(key == "mqtt"){
          data[key] = this.getSubcriptionArray(data[key]);
        }
      });
      await axios.post('import', data).then(r => {
        this.items = {};
        this.tabs = [];
        if (this.$route.query.tab) this.$router.push('app-manager')
        this.dialogResponse = true;
        this.responses = r;
      })
      .catch(err => {
        this.$toasted.show(`Import Fail!`, { type: 'error' });
      });
    },
    getSubcriptionArray(mqtt){
      let subscriptions = [];
      let indexs = [];
      let subscriptionKeys = [];
      Object.keys(mqtt).forEach(key =>{
        let keys = key.split('.');
        let subscriptionKey = "";
        let subscriptionIndex = null;
        if(keys.length > 1){
          subscriptionKey = keys[1];
          if(!subscriptionKeys.includes(subscriptionKey)){
            subscriptionKeys.push(subscriptionKey);
          }
          subscriptionIndex = Number(keys[0].split("[")[1].slice(0, -1));
          if((subscriptionIndex || subscriptionIndex === 0) && !indexs.includes(subscriptionIndex)){
            indexs.push(subscriptionIndex);
          }
        }
      })
      _forEach(indexs, i=>{
        let subscription = {};
        _forEach(subscriptionKeys,sk=>{
          subscription = {...subscription, [sk]: mqtt[`subscription[${i}].${sk}`]};
          delete mqtt[`subscription[${i}].${sk}`];
        });
        subscriptions.push(subscription);
      })
      mqtt.subscriptions = subscriptions;
      return mqtt;
    },
  },
};
</script>

