<template>
  <div>
    <v-tabs v-model="tab" dark>
      <v-tab :key="0">Rubix Points &#60;&#62; BACnet Points </v-tab>
      <v-tab :key="1">LoRa Points &#60;&#62; Rubix | BACnet Points </v-tab>
      <v-tab :key="2">Modbus Points &#60;&#62; Rubix | BACnet Points </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="0">
        <rubix-bacnet-points-mapping></rubix-bacnet-points-mapping>
      </v-tab-item>
      <v-tab-item :key="1">
        <lora-generic-bacnet-points-mapping></lora-generic-bacnet-points-mapping>
      </v-tab-item>
      <v-tab-item :key="2">
        <modbus-generic-bacnet-points-mapping></modbus-generic-bacnet-points-mapping>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>
<script>
import RubixBacnetPointsMapping from './RubixBacnetPointsMapping.vue';
import LoraGenericBacnetPointsMapping from './LoraGenericBacnetPointsMapping';
import ModbusGenericBacnetPointsMapping from './ModbusGenericBacnetPointsMapping';
export default {
  name: 'service-mapping',
  components: { RubixBacnetPointsMapping, LoraGenericBacnetPointsMapping, ModbusGenericBacnetPointsMapping },
  data: () => ({
    tab: 0,
    tabs: ['rubix-bacnet-points-mapping', 'lora-generic-bacnet-mapping', 'modbus-generic-bacnet-mapping'],
    isDisabled: false,
  }),
  watch: {
    '$route.query.tab': function (val) {
      this.tab = this.tabs.indexOf(val);
    },
    'tab': function (val, oldVal) {
      const { tab } = this.$route.query;
      if (tab !== this.tabs[val]) {
        const tabKey = this.tabs[val];
        this.$router.push({ path: 'service-mapping', query: { tab: tabKey } });
      }
    },
  },
  mounted() {
    const { tab: tabKey } = this.$route.query;
    this.tab = this.tabs.indexOf(tabKey);
  },
};
</script>
